import {Component} from "@angular/core";
import {NavController} from "ionic-angular";
import {raffleservice} from "../../services/trip-service";
import {TripDetailPage} from "../trip-detail/trip-detail";

@Component({
  selector: 'page-raffles',
  templateUrl: 'raffles.html'
})
export class RafflesPage {
  // list of raffles
  public raffles: any;

  constructor(public nav: NavController, public raffleservice: raffleservice) {
    // set sample data
    this.raffles = raffleservice.getAll();
  }

  // view trip detail
  viewDetail(id) {
    this.nav.push(TripDetailPage, {id: id});
  }
}



