import {Injectable} from "@angular/core";
import {raffles} from "./mock-raffles";

@Injectable()
export class raffleservice {
  private raffles: any;

  constructor() {
    this.raffles = raffles;
  }

  getAll() {
    return this.raffles;
  }

  getItem(id) {
    for (var i = 0; i < this.raffles.length; i++) {
      if (this.raffles[i].id === parseInt(id)) {
        return this.raffles[i];
      }
    }
    return null;
  }

  remove(item) {
    this.raffles.splice(this.raffles.indexOf(item), 1);
  }
}



